.news-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.news-subtitle {
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  font-size: 1.5rem;
}

.news-main-title {
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  font-size: 2rem;
}

.news-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.news-item {
  background-color: #364d31;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
  border: 2px solid #000;
}

.news-image {
  width: 100%;
  height: 200px;
  object-fit: contain;
}

.created-date,
.news-title {
  color: #e1d798;
  font-weight: bolder;
  margin-top: 1rem;
}

.created-by-name {
  opacity: 0.5;
}

.news-title {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  height: 75px;
}

.news-description {
  font-size: 0.8rem;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
    margin-top: 1rem;
}

@media (max-width: 576px) {
  .news-main-title {
    font-size: 1.5rem;
    text-align: center;
  }

  .news-list {
    flex-direction: column;
  }

  .news-item {
    width: 90%;
    margin-bottom: 1rem;
  }

  .news-title {
    height: auto;
  }
}
