.tournaments-container {
  position: relative;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tournaments-hero {
  padding: 1rem;
  border-radius: 1rem;
}

.tournaments-subtitle {
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  font-size: 1.5rem;
}

.tournaments-main-title {
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  font-size: 2rem;
}

.tournament-list {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  padding: 1rem;
}

.tournament-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #e1d798;
  width: 30%;
  padding: 1rem;
  border-radius: 1rem;
  color: #364d31;
  margin-bottom: 1rem;
  border: 2px solid #000;
}

.tournament-image {
  width: 100%;
  height: 400px;
  object-fit: contain;
}

.tournament-name {
  font-size: 1.5rem;
}

.tournament-sub-description {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
}

.tournament-sub-description span {
  margin-left: 0.5rem;
  opacity: 0.7;
  font-weight: normal;
}

.tournament-actions {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 1rem;
}

.watch-button,
.join-button {
  background-color: #364d31;
  padding: 1rem;
  border-radius: 0.25rem;
  clip-path: polygon(0% 0%, 90% 0, 100% 30%, 100% 100%, 0 100%);
  color: #fff;
  margin: 1rem;
  width: 45%;
}

.join-button {
  clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%, 0 30%);
}

@media (max-width: 576px) {
  .tournaments-hero {
    width: 90%;
  }

  .tournaments-main-title {
    font-size: 1.5rem;
    text-align: center;
  }

  .tournament-list {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .tournament-item {
    width: 90%;
  }

  .tournament-image {
    height: auto;
  }

  .tournament-description {
    height: auto;
  }
}
