.header {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #364d31;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  height: 80px;
  width: 100%;
  z-index: 1000;
}

.logo {
  font-family: 'Saira Stencil One', cursive;
  font-size: 2.5rem;
  display: flex;
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
}

.point {
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  margin-left: 0.5rem;
}

.navigation-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 99;
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
}

.navigation-link {
  position: relative;
  display: inline-block;
  padding: 0 30px;
  line-height: 1.25;
  color: #fff;
}

.navigation-link span {
  position: relative;
  z-index: 3;
}

.menu-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  display: none;
  font-size: 1.5rem;
  background-color: #768949;
  border-radius: 0.25rem;
  position: relative;
}

.menu-button svg {
  color: #f0f0f0;
  position: relative;
  top: 0.2rem;
}

@media (max-width: 576px) {
  .menu-button {
    display: block;
    opacity: 1;
    visibility: visible;
  }

  .navigation-menu {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: rgb(225, 215, 152);
    background: linear-gradient(45deg, #e1d798 0%, #798d4b 50%, #364d31 100%);
    transition: all 1s ease-in-out;
    transform: translateY(-100vh);
  }

  .header .responsive_nav {
    transform: none;
  }

  .close-button {
    position: absolute;
    top: 2rem;
    right: 2rem;
    z-index: 10;
  }

  .navigation-link {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
}
