.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.contact-main-title {
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
}

.input-container {
  width: 50%;
  padding: 1rem;
  border: 2px solid #000;
  border-radius: 1rem;
  background-color: #798d4b;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.input-full-name,
.input-email {
  width: 45%;
  color: #364d31;
}

.input-message {
  width: 100%;
  color: #364d31;
}

input::placeholder,
textarea::placeholder {
  color: #364d31;
}

input,
textarea {
  border: 2px solid #364d31;
  padding: 1rem;
  display: flex;
  align-items: center;
  background-color: #fff !important;
  border-radius: 0.5rem;
  margin: 1rem;
}

.send-button {
  width: 30%;
  background-color: #364d31;
  margin: auto;
  padding: 1rem;
  border-radius: 0.25rem;
  clip-path: polygon(0% 0%, 90% 0, 100% 30%, 100% 100%, 0 100%);
  color: #fff;
}

@media (max-width: 576px) {
  .tournaments-main-title {
    font-size: 1.5rem;
  }

  .input-container {
    width: 90%;
    flex-direction: column;
  }

  .input-full-name,
  .input-email {
    width: 90%;
  }

  .input-message {
    width: 90%;
  }
}
