.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.home-hero {
  padding: 1rem;
  border-radius: 1rem;
  width: 35%;
  border-radius: 2rem;
}

.home-main-title {
  margin: 1rem 0;
  font-size: 4rem;
  color: #e1d798;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  text-transform: capitalize;
  line-height: 1.5;
}

.home-main-description {
  font-size: 2rem;
  color: #fff;
  text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
    -1px -1px 0 #000;
  text-transform: capitalize;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.home-join-button {
  background-color: #364d31;
  margin: 0.5rem 0;
  width: 15%;
  padding: 1rem;
  clip-path: polygon(0% 0%, 90% 0, 100% 30%, 100% 100%, 0 100%);
  color: #fff;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 576px) {
  .home-hero {
    width: 90%;
  }

  .home-main-title {
    font-size: 2rem;
    text-align: center;
  }

  .home-main-description {
    font-size: 1.5rem;
    text-align: center;
  }

  .home-join-button {
    width: 50%;
  }
}
