*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1,
h2,
h3 {
  font-family: 'Saira Stencil One', cursive;
}

a,
img,
time,
span,
input,
textarea,
button {
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
textarea,
button {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

input,
textarea {
  width: 100%;
}

address {
  font-style: normal;
}

li {
  list-style: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  line-height: 2;
  overflow-x: hidden;
  background: rgb(225,215,152);
  background: radial-gradient(circle, #e1d798 0%, #798d4b 50%, #364d31 100%);
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: #18171f;
}

::-webkit-scrollbar-thumb {
  background-color: #768949;
}
